
/*css 部分*/
.circleProgress_wrapper[data-v-7cf63e57]{ /*设置圆环的大小*/
  margin: auto;
  position: relative;
  width: 100px;
  height: 100px;
  overflow: hidden;
  border-radius: 50%;
  box-sizing: border-box;
  cursor: pointer;
}
.circleProgress_wrapper[data-v-7cf63e57]:before{
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.2);
  background-size: cover;
  border-radius: 50%;
}
.circleProgress_wrapper.rotate[data-v-7cf63e57]:before{
  -webkit-animation:audioLogoRotate 10s linear infinite;
          animation:audioLogoRotate 10s linear infinite;
}
@-webkit-keyframes audioLogoRotate{
0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
}
100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes audioLogoRotate{
0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
}
100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.circleProgress_wrapper[data-v-7cf63e57]:after{
  position: absolute;
  top: 4px;
  left: 4px;
  content: "";
  width: 92px;
  height: 92px;
  border-radius: 50%;
  background-color:rgba(253,253,253,0.3); 
  background-image:url('data:image/png;base64,');
  background-repeat:no-repeat;
  background-position:center;
  background-size:18px 22px;
}
.circleProgress_wrapper.rotate[data-v-7cf63e57]:after{
  background-image:url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAiCAMAAAAJbCvNAAAAM1BMVEX///8AAAD///////////////////////////////////////////////////////////+Q8lQ3AAAAEXRSTlPmAOEXHcLcvrtvZlER1IyHDEwQIaQAAABJSURBVDjL7ctbEoAgDEPRBLWK4mP/q/XDIGUJzPT+NXOKY1u+1p2qm2D4ewTONhXCNQmkNs0BAgQYFrirCJj/Qa4i3VTZ6nSRL9xqAzIch8kgAAAAAElFTkSuQmCC');
  background-size:18px 22px;
}
/*这是一个街区了左半部分，只显示右半部分的div*/
.audiomini[data-v-7cf63e57]{
  position: absolute;/*必须是绝对定位元素，clip属性才会有效*/
  width: 100%;
  height: 100%;
  clip: rect(0,100px,100px,50px);
}
.audiomini.gt50[data-v-7cf63e57]{/*当进度超过50%时，取消裁剪*/
  clip: rect(auto, auto, auto, auto);
}
.bar[data-v-7cf63e57],.fill[data-v-7cf63e57]{/*两个只显示左半部分的半圆*/
  position: absolute;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: 4px rgba(255, 255, 253, 1) solid;
  border-radius: 50%;
  clip: rect(0, 50px, 100px, 0);
}
.audiomini.gt50 .fill[data-v-7cf63e57]{/*当进度超过50%时，让fill旋转180度填充50%*/
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.loadingDiv[data-v-7cf63e57]{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index:1;
  border-radius: 100%;
  background-color:rgba(255, 255, 253, .6);
}
.loadingAnimate[data-v-7cf63e57] {
  width: 100%;
  height: 100%;
  position: absolute;
  background-repeat:no-repeat;
  background-position:center;
  z-index:2;
  background-image:url("../assets/loading.gif")
}



